import Vue from 'vue'
import VeeValidate from 'vee-validate'
import { Validator } from 'vee-validate';
import zh_CN from 'vee-validate/dist/locale/zh_CN'
import VueI18n from 'vue-i18n';
Vue.use(VueI18n)

const i18n = new VueI18n({
    locale: 'zh_CN',
})

Vue.use(VeeValidate, {
    i18n,
    i18nRootKey: 'validation',
    dictionary: {
        zh_CN
    }
});

//email required url include min max max_value ip between ....

/**
 * 
 */
const cutomsRules = {
    "posInt": { //
        getMessage(field, args) {
            //
            return ""
        },
        validate(value, args) {
            //
            if (!value) return true;
            var posInt_REGEXP = /^[1-9][0-9]*$/;
            return posInt_REGEXP.test(value);
        }
    },
    "isInt": { //
        getMessage(field, args) {
            //
            return ""
        },
        validate(value, args) {
            //
            if (!value) return true;
            var posInt_REGEXP = /^[0-9][0-9]*$/;
            return posInt_REGEXP.test($.trim(value));
        }
    },
    'twoTecimal': {
        getMessage(field, args) {
            return "0";
        },
        validate(value, args) {
            if (!value) return true;
            var posInt_REGEXP = /^\d+(\.\d{1,2})?$/;
            return posInt_REGEXP.test(value);
        }
    },
    'maxNumLength': {
        getMessage(field, args) {
            return "12";
        },
        validate(value, args, data) {
            if (!value) return true;
            var numberLimit = 12;
            if (args.length > 0) {
                numberLimit = parseInt(args[0]);
            }
            var num = value.toString().split(".");
            return num[0].length < numberLimit + 1;
        },
    },
    "Http": {
        getMessage(field, args) {
            return "URLhttp://www.baidu.com";
        },
        validate(value, args) {
            if (!value) return true;
            var posInt_REGEXP = /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+)\.)+([A-Za-z0-9-~\/])+$/;
            return posInt_REGEXP.test(value);
        }
    },
    'noLonger40Chracter': {
        getMessage(field, args) {
            return "40";
        },
        validate(value, args) {
            if (!value) return true;
            var regCh = /[\u4e00-\u9fa5]/; //
            var count = 0;
            for (var i = 0; i < value.length; i++) {
                if (regCh.test(value[i]))
                    count += 2;
                else
                    count += 1;
            }
            if (count > 80) {
                return false;
            }
            return true
        }
    },
}

/**
 * 
 */
const dictionary = {
    zh_CN: {
        messages: {
            email: () => '',
            required: (name) => `${name}`,
            maxNumLength(number, params, data) {
                return `${number}${params[0]?12:params[0]}`;
            },
        }
    },
};

/**
 * 
 */
const dict = {
    custom: {
        email: {
            required: ''
        },
        module: {
            required: ''
        }
    }
};

const install = function() {
    Object.keys(cutomsRules).forEach(key => {
        Validator.extend(key, cutomsRules[key]);
    });
}

install();
Validator.localize(dictionary);
Validator.localize('zh_CN', dict);
