import Vue from 'vue';
Vue.prototype.FixTable = function(TableID, FixColumnNumberRight, FixColumnNumberLeft) {
	//
	var fixtableHeight = $("#" + TableID).height();
	var fixtableWidth = $("#" + TableID).width();
	//
	var parentWidth = $("#" + TableID).parent().width();
	//
	var colNum = $("#" + TableID + " tr:last td:last").index();
	//
	var scrollWidth = getScrollbarWidth();
	//
	var overflowX = "auto";
	var overflowY = "hidden";
	//  TableID_tableLayoutTableIDTableID_tableLayout
	if($("#" + TableID + "_tableLayout").length != 0) {
		$("#" + TableID + "_tableLayout").before($("#" + TableID));
		$("#" + TableID + "_tableLayout").empty();
	} else {
		//TableID_tableLayout
		$("#" + TableID).after("<div id='" + TableID + "_tableLayout' style='overflow:hidden;height:" + (fixtableHeight + scrollWidth) + "px; width:" + fixtableWidth + "px;'></div>");
	}
	//divTableID_tableLayout
	$('<div id="' + TableID + '_tableFix"></div>' +
		'<div id="' + TableID + '_tableFixLeft"></div>' +
		'<div id="' + TableID + '_tableData"></div>').appendTo("#" + TableID + "_tableLayout");
	//tablejquery
	var oldtable = $("#" + TableID);
	//
	var tableFixClone = oldtable.clone(true);
	//id
	tableFixClone.attr("id", TableID + "_tableFixClone");
	//TableID__tableFix
	$("#" + TableID + "_tableFix").append(tableFixClone);
	//
	var tableFixLeftClone = oldtable.clone(true);
	//id
	tableFixLeftClone.attr("id", TableID + "_tableFixLeftClone");
	//TableID__tableFix
	$("#" + TableID + "_tableFixLeft").append(tableFixLeftClone);
	//

	//_tableData
	$("#" + TableID + "_tableData").append(oldtable);
	//
	$("#" + TableID + "_tableLayout table").each(function() {
		$(this).css("margin", "0");
	});
	var ColumnsWidth = 0;
	var ColumnsNumber = 0;
	var ColumnsLeftWidth = 0;
	var ColumnsLeftNumber = 0;
	$("#" + TableID + "_tableData tr:last td:lt(" + FixColumnNumberRight + ")").each(function() {
		ColumnsWidth += $(this).outerWidth(true);
		ColumnsNumber++;
	});
	//
	$("#" + TableID + "_tableData tr:last td:gt(" + (colNum - FixColumnNumberLeft) + ")").each(function() {
		ColumnsLeftWidth += $(this).outerWidth(true);
		ColumnsLeftNumber++;
	});
	ColumnsWidth += 2;
	ColumnsLeftWidth += 2;
	//
	$("#" + TableID + "_tableFix").css("width", ColumnsWidth);
	$("#" + TableID + "_tableFixLeft").css("width", ColumnsLeftWidth);
	//_tableData
	$("#" + TableID + "_tableData").scroll(function() {});
	//_tableFix
	$("#" + TableID + "_tableFix").css({
		"overflow": "hidden",
		"position": "relative",
		"z-index": "50",
		"box-shadow": "1px 0 8px #d3d4d6",
		"background-color": "#fafafa",
	});
	$("#" + TableID + "_tableFixClone").css({
		"width": fixtableWidth + "px",
	});
	//
	$("#" + TableID + "_tableData").css({
		"width": parentWidth,
		"height": fixtableHeight + scrollWidth,
		"position": "relative",
		"z-index": "35"
	});
	$("#" + TableID + "_tableData").css({
		"overflow-x": overflowX,
		"overflow-y": overflowY
	});
	$("#" + TableID + "_tableFix").offset($("#" + TableID + "_tableLayout").offset());
	$("#" + TableID + "_tableFixLeft").offset($("#" + TableID + "_tableLayout").offset());
	$("#" + TableID + "_tableData").offset($("#" + TableID + "_tableLayout").offset());
	$("#" + TableID + "_tableFixLeft").css({
		"overflow": "hidden",
		"position": "relative",
		"z-index": "50",
		"background-color": "#fafafa",
		"box-shadow": "1px 0 8px #d3d4d6",
		"left": (parentWidth - ColumnsLeftWidth) + "px",
	});

	$("#" + TableID + "_tableFixLeft").children("table").css({
		"margin-left": -(fixtableWidth - ColumnsLeftWidth) + "px"
	});

	function getScrollbarWidth() {
		var oP = document.createElement('p'),
			styles = {
				width: '100px',
				height: '100px',
				overflowY: 'scroll',
			},
			i, scrollbarWidth;

		for(i in styles) {
			oP.style[i] = styles[i];
		}
		document.body.appendChild(oP);
		scrollbarWidth = oP.offsetWidth - oP.clientWidth;
		oP.remove();

		return scrollbarWidth;
	}
}
//dom
Vue.prototype.FixTableForCSS = function(TableID, FixColumnNumberRight, FixColumnNumberLeft) {
	//
	var fixtableHeight = $("#" + TableID).height();
	var fixtableWidth = $("#" + TableID).width();
	//
	var parentWidth = $("#" + TableID).parent().width();
	//
	var colNum = $("#" + TableID + " tr:last td:last").index();
	//
	var scrollWidth = getScrollbarWidth();
	//
	var overflowX = "auto";
	var overflowY = "hidden";
	//TableID_tableLayout
	$("#" + TableID + "_tableLayout").css({
		"overflow": "hidden",
		"height": (fixtableHeight + scrollWidth) + "px",
		"width": fixtableWidth + "px",
	});
	//
	$("#" + TableID + "_tableLayout table").each(function() {
		$(this).css("margin", "0");
	});
	var ColumnsWidth = 0;
	var ColumnsNumber = 0;
	var ColumnsLeftWidth = 0;
	var ColumnsLeftNumber = 0;
	$("#" + TableID + "_tableData tr:last td:lt(" + FixColumnNumberRight + ")").each(function() {
		ColumnsWidth += $(this).outerWidth(true);
		ColumnsNumber++;
	});
	//
	$("#" + TableID + "_tableData tr:last td:gt(" + (colNum - FixColumnNumberLeft) + ")").each(function() {
		ColumnsLeftWidth += $(this).outerWidth(true);
		ColumnsLeftNumber++;
	});
	ColumnsWidth += 2;
	ColumnsLeftWidth += 2;
	//
	$("#" + TableID + "_tableFix").css("width", ColumnsWidth);
	$("#" + TableID + "_tableFixLeft").css("width", "100%");
	//_tableData
	$("#" + TableID + "_tableData").scroll(function() {});
	//_tableFix
	$("#" + TableID + "_tableFix").css({
		"overflow": "hidden",
		"position": "relative",
		"z-index": "50",
		"box-shadow": "1px 0 8px #d3d4d6",
		"background-color": "#fafafa",
	});
	$("#" + TableID + "_tableFixClone").css({
		"width": fixtableWidth + "px",
	});
	//
	$("#" + TableID + "_tableData").css({
		"width": parentWidth,
		"height": fixtableHeight + scrollWidth + 4,
		"position": "relative",
		"z-index": "35"
	});
	$("#" + TableID + "_tableData").css({
		"overflow-x": overflowX,
		"overflow-y": overflowY
	});
	$("#" + TableID + "_tableFix").offset($("#" + TableID + "_tableLayout").offset());
	$("#" + TableID + "_tableFixLeft").offset($("#" + TableID + "_tableLayout").offset());
	$("#" + TableID + "_tableData").offset($("#" + TableID + "_tableLayout").offset());
	$("#" + TableID + "_tableFixLeft").css({
		"overflow": "hidden",
		"position": "relative",
		"z-index": "50",
		"box-shadow": "1px 0 8px #d3d4d6",
		"left": (parentWidth - ColumnsLeftWidth) + "px",
	});

	$("#" + TableID + "_tableFixLeft").children("table").css({
		"margin-left": -(fixtableWidth - ColumnsLeftWidth) + "px"
	});

	function getScrollbarWidth() {
		var oP = document.createElement('p'),
			styles = {
				width: '100px',
				height: '100px',
				overflowY: 'scroll',
			},
			i, scrollbarWidth;

		for(i in styles) {
			oP.style[i] = styles[i];
		}
		document.body.appendChild(oP);
		scrollbarWidth = oP.offsetWidth - oP.clientWidth;
		oP.remove();
		return scrollbarWidth;
	}
}