/**
 * 
 */
export default {
	bind(el, binding, vnode) {

	},
	inserted(el, binding, vnode) {
		function documentHandler() {
			let tableHeight=0,operBoxHeight=0,filterBoxHeight=0;
			let conheight = $(".view_body")[0].getClientRects()[0].height;//
			if($(".lay-operation")[0]){
				operBoxHeight = $(".lay-operation")[0].getClientRects()[0].height;//
			}
			if($(".lay-filter")[0]){
				filterBoxHeight = $(".lay-filter")[0].getClientRects()[0].height;//
			}
			// 
			if(binding.value == 'tablebox') {
				tableHeight = conheight-operBoxHeight-filterBoxHeight-40;//40 20px
				el.style.height = tableHeight + 'px';
			}
			if(binding.value == 'table'){
				tableHeight = conheight-operBoxHeight-filterBoxHeight-40-150;//
				el.style.minHeight = tableHeight + 'px';
			}
		}
		documentHandler();
		el.__vueAutoheight__ = documentHandler;
		window.addEventListener('resize', documentHandler);
	},
	update(el) {},
	unbind(el, binding) {
		window.removeEventListener('resize', el.__vueAutoheight__);
		delete el.__vueAutoheight__;
	}
}